.section-1 {
  /*
    DEMO STYLE
*/

  @import "https://fonts.googleapis.com/css?family=Poppins:300,400,500,600,700";
  body {
    font-family: "Poppins", sans-serif;
    background: #1c1c1c !important;

    margin: 0%;
  }

  .main {
    position: absolute;
    top: 0;
    left: 0;
    z-index: 999;
    width: 100%;
  }
  #content {
    display: none;
  }

  .components {
    text-align: start;
  }

  .sidebar-footer {
    margin-top: 185px;
  }

  .links1 {
    margin-right: 10px;
    margin-left: 10px;
  }
  .dkmsmmkss {
    position: absolute;
    left: 50%;
    bottom: 0px;
    transform: translateX(-50%);
  }
  .dkmsmmks {
    width: unset !important;
  }
  .CTAs {
    margin: 0 auto;
    width: 100%;
  }

  a,
  a:hover,
  a:focus {
    color: inherit;
    text-decoration: none;
    transition: all 0.3s;
  }

  .navbar {
    padding: 15px 10px;
    background: #fff;
    border: none;
    border-radius: 0;
    margin-bottom: 40px;
    box-shadow: 1px 1px 3px rgba(0, 0, 0, 0.1);
  }

  .navbar-btn {
    box-shadow: none;
    outline: none !important;
    border: none;
  }

  .line {
    width: 100%;
    height: 1px;
    border-bottom: 1px dashed #ddd;
    margin: 40px 0;
  }

  .wrapper {
    display: flex;
    width: 100%;
    align-items: stretch;
  }

  #sidebar {
    min-width: 100%;
    max-width: 100%;
    background: #202226;
    color: #fff;
    transition: all 0.3s;
    height: 100%;
    li {
      a {
        padding-left: 50px !important;
      }
      // padding-left: 40px;
      // margin-left: 40px;
    }
  }
  .gg {
    display: flex !important;
    justify-content: start;
    align-items: center !important;
    h6 {
      padding-left: 17px;
      margin-bottom: 0px !important;
    }
  }
  .left_side {
    background-color: #1c1c1c;
  }
  .right_side {
    background-color: #000000;
  }
  #sidebar.active {
    margin-left: -250px;
  }
  .bg-img {
    background-color: #000000;
    height: 200vh;
    position: relative;
    .upper_image {
      position: absolute;
      top: 0;
    }
    .down_image {
      position: absolute;
      bottom: 0;
      width: 100%;
    }
  }
  #sidebar .sidebar-header {
    padding: 20px;
    background: #202226;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
  }

  #sidebar ul.components {
    padding: 20px 0;
  }

  #sidebar ul p {
    color: #fff;
    padding: 10px;
  }

  #sidebar ul li a {
    padding: 10px;
    font-size: 1.1em;
    display: block;
  }

  #sidebar ul li a:hover {
    color: #ffb805;
    background: #161616;
  }
  .bg-img {
    img {
      width: 100%;
    }
  }

  a[data-toggle="collapse"] {
    position: relative;
  }

  .dropdown-toggle::after {
    display: block;
    position: absolute;
    top: 50%;
    right: 20px;
    transform: translateY(-50%);
  }

  ul ul a {
    font-size: 0.9em !important;
    padding-left: 30px !important;
    background: #6d7fcc;
  }

  .CTAs {
    padding: 20px;
  }

  .CTAs a {
    text-align: center;
    font-size: 0.9em !important;
    display: block;
    border-radius: 5px;
    margin-bottom: 5px;
  }

  a.download {
    background: #fff;
    color: #7386d5;
  }

  /* ---------------------------------------------------
    CONTENT STYLE
----------------------------------------------------- */

  #content {
    width: 100%;
    padding: 20px;
    min-height: 100vh;
    transition: all 0.3s;
  }
  .card-sidebar {
    background-color: #000;
  }
  .kdjdkjfdk {
    position: absolute !important;
    // z-index: 1111111111 !important;
    top: 0;
    min-height: 100vh;
    width: 100%;
    height: 100%;
    display: none;
  }
  .mobileview {
    position: absolute !important;
    z-index: 1111111111 !important;
    top: 0;
    min-height: 100vh;
    width: 264px;
    height: 100%;
    display: block;
  }
}
.section-1 {
  position: relative;
}
.button_toggle {
  position: absolute;
  z-index: 11111;
  right: 10px;
  top: 10px;
  button {
    background-color: transparent;
    border: none;
    color: #ffb805;
    font-size: 24px;
    border: 2px solid #ffb805;
    border-radius: 6px;
  }
}
.section-1 .CTAs {
  display: flex !important;
  justify-content: center;
  align-items: center;
}
.mint-11{
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    position: absolute;
    top: 400px;
    left: 0;
    z-index: 2000;
    transform: translateY(-44%, -50%);
    button{
        background-color: #ffb805;
        padding: 15px 55px;
        border-radius: 10px;
        border: none;
        margin-bottom: 40px;
    }
    h4{
        font-size: 45px;
        font-weight: 400;
        color: #fff;
    }
}
.para-main{
  font-style: normal;
  font-weight: 500;
  font-size: 18px;
  line-height: 35px;
  color: #aeaaaa;
  text-align: justify;
}
.heading-main{
  font-style: normal;
  font-weight: 600 !important;
  font-size: 45px;
  line-height: 135%;
  text-align: center;
  letter-spacing: 0.1em;
  color: #E4D469 !important;
  margin: 20px 0;
}
/* ---------------------------------------------------
    MEDIAQUERIES
----------------------------------------------------- */
