    .section-1 .sidebar-footer{
      margin-top: 100% !important;
    }
}
@media (max-width: 1680px) {
  .section-1 .bg-img{
    height: 220vh;
  }
   
}
@media (max-width: 1440px) {
  .section-1 .bg-img{
    height: 226vh;
  }
  .heading-main{
    font-size: 36px !important;
  }
}
@media (max-width: 1300px) {
    
}
@media (max-width: 1199px) {
    
    .section-1 .sidebar-footer{
      margin-top: 85% !important;
    }
