      width: 700px;
      top: 261px !important;
      left: 36px;
    }
}
@media (max-width:1100px){
  .section-1 .sidebar-footer{
    margin-top: 65% !important;
  }
}
@media (max-width: 600px) {
  .mint-11{
    width: 382px;
    top: 177px !important;
    left: 22px;
  }
  .section-1 .bg-img{
    height: 234vh;
  }
}
@media (max-width: 380px) {
  .mint-11{
    width: 335px;
  }
