    font-size: 22px !important;
}
.para-main{
  font-size: 16px !important;
}
}
@media (max-width: 2560px) {
  .button_toggle {
    display: none!important;
  }
  .kdjdkjfdk{
      display: block!important;
  }
}
@media (max-width: 768px) {
  .button_toggle {
    display: block!important;
  }
  .kdjdkjfdk{
    display: none!important;
}
  #sidebar {
    margin-left: 0px;
  }
  #sidebar.active {
    margin-left: 0;
  }
  #sidebarCollapse span {
    display: none;
  }
}
.btn {
  .btn-1 {
    align-items: center;
    padding: 12px 25px;
    background: #1c1c1c;
    border-radius: 10px;
    color: #ffffff;
  }
}


.button_toggle button{
    outline: none;
}

@media (max-width:360px){
  .button_toggle button{
    font-size: 15px !important;
  }
  .mint-11 {
    width: 278px;
}
.section-1 .bg-img{
  height: 253vh;
}
}





